import { Button, Form, InputNumber, Select, Space } from 'antd'
import { Data, Step3Values } from '../App'
import { FC, useState } from 'react'
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';

export interface Step3Props {
  current: number;
  option?: Data[]
  nextClick: (values: { dishs: Step3Values[] }) => void
  prev: () => void
}

const Step3: FC<Step3Props> = ({ current, option, nextClick, prev }) => {
  const display = current === 2 ? 'block' : 'none'
  const [form] = Form.useForm()
  const [selectds, setSelectds] = useState<string[]>([])

  // 添加已选菜品
  const selectdAdd = (name: string, index: number) => {
    const selectdsCopy = [...selectds]
    selectdsCopy.splice(index, 1, name)
    setSelectds(selectdsCopy)
  }

  // 删除已选菜品
  const selectdRemove = (index: number) => {
    const selectdsCopy = [...selectds]
    selectdsCopy.splice(index, 1)
    setSelectds(selectdsCopy)
  }

  // 根据菜品是否已选禁用选项
  const optionDisabled = (name: string) => selectds.some(item => item === name)

  return (
    <Form style={{ display }} form={form} onFinish={nextClick}>
      <Form.List name="dishs">
        {(fields, { add, remove }) => (
          <>
            {fields.map(field => (
              <Space key={field.key} align='baseline' style={{ width: '100%' }}>
                <Form.Item label='菜品' name={[field.name, 'dish']} rules={[{ required: true, message: '请选择菜品' }]}>
                  <Select placeholder='请选择' onChange={((value: string) => selectdAdd(value, field.name))}>
                    {option?.map(item => <Select.Option key={item.id} value={item.name} disabled={optionDisabled(item.name)}>{item.name}</Select.Option>)}
                  </Select>
                </Form.Item>

                <Form.Item label='份量' name={[field.name, 'servings']} initialValue={1} rules={[{ required: true, message: '请输入分量' }]}>
                  <InputNumber min={1} max={10} />
                </Form.Item>

                <MinusCircleOutlined onClick={() => {
                  remove(field.name)
                  selectdRemove(field.name)
                }} />

              </Space>
            ))}
            {option?.length && (fields.length < option?.length) &&
              <Form.Item>
                <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                  添加菜品
                </Button>
              </Form.Item>}
          </>
        )}
      </Form.List>



      <Form.Item wrapperCol={{ span: 6, offset: 18 }} >
        <Space>
          <Button
            onClick={() => {
              prev()
              form.resetFields()
              form.setFieldsValue({ dishs: [] })
              setSelectds([])
            }}
          >
            上一步
          </Button>
          <Button type='primary' htmlType='submit'>下一步</Button>
        </Space>
      </Form.Item>
    </Form >
  )
}

export default Step3
