import { Button, Col, Descriptions, Row, Space, message } from 'antd';
import React, { FC } from 'react'
import { Step1Values, Step2Values, Step3Values } from '../App'

export interface Step4Props extends Step1Values, Step2Values {
  dishs: Step3Values[];
  prve: () => void;
  current: number
}

const Step4: FC<Step4Props> = (props) => {
  const display = props.current === 3 ? 'block' : 'none'

  return (
    <div className='descWarp' style={{ display }} >
      <Descriptions >
        <Descriptions.Item label="餐点">{props.meal}</Descriptions.Item>
        <Descriptions.Item label="人数">{props.quantity}</Descriptions.Item>
        <Descriptions.Item label="餐厅">{props.restaurant}</Descriptions.Item>
        <Descriptions.Item label="菜品"><div>{props.dishs.map(item => <div key={item.dish}>{item.dish} * {item.servings}</div>)}</div></Descriptions.Item>
      </Descriptions>
      <Row>
        <Col span={6} offset={18}>
          <Space>
            <Button onClick={props.prve} >
              上一步
            </Button>
            <Button type='primary' onClick={() => message.success('提交成功')} >提交</Button>
          </Space>
        </Col>
      </Row>
    </div>
  )
}

export default Step4
