import { Button, Form, InputNumber, Select } from 'antd'

import { FC } from 'react'
import { Step1Values } from '../App'

export interface Step1Props {
  current: number
  values?: Step1Values
  nextClick: (values: Step1Values) => void
}

const Step1: FC<Step1Props> = ({ current, nextClick }) => {
  const display = current === 0 ? 'block' : 'none'

  return (
    <Form labelCol={{ span: 18 }} wrapperCol={{ span: 6 }} style={{ display }} onFinish={nextClick}>
      <Form.Item label='餐点' name='meal' rules={[{ required: true, message: '请选择类别' }]}>
        <Select placeholder='请选择' >
          <Select.Option value='breakfast'>早餐</Select.Option>
          <Select.Option value='lunch'>午餐</Select.Option>
          <Select.Option value='dinner'>晚餐</Select.Option>
        </Select>
      </Form.Item>
      <Form.Item label='人数' name='quantity' rules={[{ required: true, message: '请输入人数' }]}>
        <InputNumber placeholder='请输入' max={10} min={1} />
      </Form.Item>
      <Form.Item wrapperCol={{ span: 6, offset: 18 }} >
        <Button type='primary' htmlType='submit' >下一步</Button>
      </Form.Item>
    </Form>
  )
}

export default Step1
