import { Button, Form, Select, Space } from 'antd'

import { Data } from '../App'
import { FC } from 'react'

export interface Step2Props {
  current: number;
  option?: Data[]
  nextClick: (values: any) => void
  prev: () => void
}

const Step2: FC<Step2Props> = ({ current, option, nextClick, prev }) => {
  const display = current === 1 ? 'block' : 'none'
  const [form] = Form.useForm()

  return (
    <Form form={form} labelCol={{ span: 18 }} wrapperCol={{ span: 6 }} style={{ display }} onFinish={nextClick} onChange={() => { }}>
      <Form.Item label='餐厅' name='restaurant' rules={[{ required: true, message: '请选择餐厅' }]}>
        <Select placeholder='请选择' >
          {option?.map(item => <Select.Option key={item.id} value={item.restaurant}>{item.restaurant}</Select.Option>)}
        </Select>
      </Form.Item>
      <Form.Item wrapperCol={{ span: 6, offset: 18 }} >
        <Space>
          <Button
            onClick={() => {
              prev()
              form.resetFields()
            }}
          >
            上一步
          </Button>
          <Button type='primary' htmlType='submit' >下一步</Button>
        </Space>
      </Form.Item>
    </Form>
  )
}

export default Step2
