import './App.css';

import Step1 from './components/Step1'
import Step2 from './components/Step2'
import Step3 from './components/Step3'
import Step4 from './components/Step4'
import StepsBar from './components/StepsBar'
import data from './data'
import { message } from 'antd';
import { uniqBy } from 'lodash'
import { useState } from 'react'

export type Meal = 'breakfast' | 'lunch' | 'dinner' | string
export interface Step1Values {
  meal: Meal;
  quantity: number;
}
export interface Step2Values {
  restaurant: string
}

export interface Step3Values {
  dish: string;
  servings: number;
}
export interface Data {
  id: number;
  name: string;
  restaurant: string;
  availableMeals: Meal[];
  disabled?: boolean
}

function App() {
  const [current, setCurrent] = useState<number>(0)
  const [step1Values, setStep1Values] = useState<Step1Values>({ meal: '', quantity: 1 })
  const [step2Values, setStep2Values] = useState<Step2Values>({ restaurant: '' })
  const [step3Values, setStep3Values] = useState<Step3Values[]>([])
  const [suitableRestaurants, setSuitableRestaurants] = useState<Data[]>()
  const [foodProvided, setFoodProvided] = useState<Data[]>()


  const suitableRestaurantFilter = (meal: Meal) => data.dishes.filter(item => item.availableMeals.includes(meal))
  const foodProvidedFilter = (restaurant: string) => suitableRestaurants?.filter(item => item.restaurant.includes(restaurant))


  const step1NextClick = (values: Step1Values) => {
    setStep1Values({ ...values })
    setSuitableRestaurants(suitableRestaurantFilter(values.meal)) // 根据餐点筛选出合适的餐厅
    setCurrent(current + 1)
  }

  const step2NextClick = (values: Step2Values) => {
    setStep2Values({ ...values })
    setFoodProvided(foodProvidedFilter(values.restaurant)) // 根据餐厅筛选出合适的的菜品
    setCurrent(current + 1)
  }

  const step3NextClick = (values: { dishs: Step3Values[] }) => {
    if (values.dishs?.length) {
      const quantity = step1Values.quantity
      let servingsTotal = 0; // 份量总和
      values.dishs.map((item: any) => servingsTotal += item.servings) 
      if (servingsTotal < quantity) {
        message.error('份量应大于或等于人数')
      } else {
        setStep3Values(values.dishs)
        setCurrent(current + 1)
      }
    } else {
      message.error('请选择菜品和份量')
    }
  }

  const prev = () => setCurrent(current - 1)

  return (
    <div className='app'>
      <StepsBar current={current} />
      <Step1 current={current} nextClick={step1NextClick} />
      <Step2
        current={current}
        option={uniqBy(suitableRestaurants, 'restaurant')}
        nextClick={step2NextClick}
        prev={prev}
      />
      <Step3
        current={current}
        option={uniqBy(foodProvided, 'name')}
        nextClick={step3NextClick}
        prev={prev}
      />
      <Step4
        {...step1Values}
        {...step2Values}
        dishs={step3Values}
        prve={prev}
        current={current}
      />
    </div>
  );
}
export default App;